.navbar{
    display: flex;
    justify-content: space-around;  /* put space around all items in flex container */      
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;   /* set shadow offset, blur, and color settings */
    align-items: center;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo img{
    width: 70px;
    height: auto;
}
.nav-logo p{
    color: #171717;
    font-size: 38px;
    font-weight: 600;
}
.nav-dropdown{ /* dropdown icon only only displays when the window width is < 800px */
    display: none;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column; /* items are arranged in a row, but doesn't seem to affect their layout */
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height: 58px;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}
.nav-login-cart button:active{  /* changes background color on click */
    background: #f3f3f3;
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;  /* negative marins move the circle up 35px and left 55px so it intersects the cart icon */
    margin-left: -55px; 
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}
.navbar a{      /* a is used to style <Link> tags */
    color: #626262;      /* removes the underline that text links have */
}
@media(max-width:1280px){
    .navbar{
        padding: 12px 50px;
    }
    .nav-logo img{
        width: 60px;
    }
    .nav-logo p{
        font-size: 25px;
    }
    .nav-menu{
        gap: 30px;
        font-size: 16px;
    }
    .nav-login-cart{
        gap: 30px;
    }
    .nav-login-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
    .nav-cart-count{
        margin-left: -40px;
        font-size: 12px;
    }
}
@media(max-width:1024px){
    .navbar{
        padding: 12px 30px;
    }
    .nav-menu{
        gap: 25px;
        font-size: 14px;
    }
    .nav-login-cart button{
        width: 80px;
        height: 35px;
        font-size: 14px;
    }
    .nav-login-cart img{
        width: 30px;
    }
    .nav-cart-count{
        width: 18px;
        height: 18px;
        
    }
}
@media(max-width:800px){
    .navbar{
        padding: 10px 0px;
    }
    .nav-dropdown{ /* dropdown icon only displays when window width is < 800px */
        display: block;
        width: 30px;
        height: auto;
        rotate: -90deg;
        transition: 0.125s;
    }
    .nav-menu{ /* when nav-menu-visible is toggled off, do not display menu items */
        display: none;
        height: 80px;
        width: 100%;
        position: absolute;
        background-color: white;
        justify-content: center;
        top: 50px;
    }
    .nav-menu-visible{ /* when dropdown_icon is clicked, and nav-menu-visible is toggled on */
        display: flex; /* display the nav-menu items */
    }
    .nav-dropdown.open{ /* if the .nav-dropdown class also has the .open class */
        transform: rotate(90deg)
    }
}
@media(max-width:500px){ /* small widths for mobile phones */
    .navbar{
        padding: 8px 0px;
        gap: 0;
    }
    .nav-logo{
        transform: scale(0.8);
    }
    .nav-menu{
        height: 70px;
        top: 50px;
    }
    .nav-login-cart{
        transform: scale(0.8);
    }
}